/* App.css */

.container {
    height: 100vh; /* Full screen height */
    display: flex; /* Use flexbox for centering */
    flex-direction: column; /* Stack items vertically */
    justify-content: center; /* Center items vertically */
    align-items: center; /* Center items horizontally */
    background-size: cover; /* Ensure any background covers the full page */
    background-position: center; /* Center any background */
    animation: fadeIn 1s ease-in-out; /* Fade-in effect */
  }
  
  @keyframes fadeIn {
    from {
      opacity: 0; /* Fully transparent at the start */
    }
    to {
      opacity: 1; /* Fully visible at the end */
    }
  }
  
  .logo {
    width: 300px; /* Set logo width */
    max-width: 100%; /* Ensure it doesnâ€™t exceed the container size */
    height: auto;
    margin-bottom: 30px; /* Space below the logo */
    margin-left: 10px; /* Add space to the left to move it right */
  }
  
  .buttons-container {
    display: flex;
    flex-direction: column; /* Stack buttons vertically */
    align-items: center; /* Center buttons horizontally */
    gap: 10px; /* Add spacing between buttons */
  }
  
  .button {
    padding: 10px 20px; /* Padding for better click area */
    font-size: 15px; /* Font size of the button text */
    font-family: "Noto Serif JP", serif; /* Use Noto Serif JP font */
    font-weight: 400; /* Regular font weight */
    background-color: rgba(255, 255, 255, 0.5); /* Semi-transparent white background */
    color: black; /* Text color */
    border: none; /* No border */
    border-radius: 5px; /* Rounded corners */
    cursor: pointer; /* Pointer cursor on hover */
    transition: background-color 0.3s ease; /* Smooth transition for hover effect */
  }
  
  .button:hover {
    background-color: rgb(220, 220, 220); /* Darker background on hover */
    color: black; /* White text on hover */
  }